import { checkLogin, getUserInfo } from './utils';

/**  */
export function defUserinfo(obj) {
  Object.defineProperty(obj, 'CJ_userInfo', {
    get() {
      return getUserInfo();
    },
    set() { }
  });
  
}

/**  */
export function defIsLogin(obj) {
  Object.defineProperty(obj, 'CJ_isLogin', {
    get() {
      return checkLogin();
    },
    set() { }
  });
}
